.footer {
  width: 880px;
  margin: 0 auto;
  padding: 66px 0 60px;
}

@media screen and (max-width: 950px) {
  .footer {
    width: 100%;
    margin: 0;
  }
}

@media screen and (max-width: 650px) {
  .footer {
    max-width: 282px;
    padding: 48px 0 36px;
    margin: 0 auto;
  }
}
